ul {
display: flex;
flex-direction: column;
list-style: none;
padding: 0 5px 0 5px;
}

li {
display: flex;
flex-direction: row;
align-items: center;
justify-content: space-between;
}

p {
font-size: 20px;
font-weight: 500;
margin-right: 30px;
}

li > button {
font-size: 15px;
font-weight: 700;
color: rgba(51, 51, 51, 0.8);
padding: 5px;
border: none;
box-shadow: 0 0 2px #333;
cursor: pointer;
background-color: rgb(224 113 113 / 74%);
border-radius: 5px;
transition-duration: 400ms;
}

li > button:hover {
transform: scale(1.1);
background-color: rgb(252 68 68 / 74%);
}

.contactsTitle {
    text-align: center;
    font-size: 30px;
}